import axios from 'axios';

const baseURL = "http://67.205.184.183:3020";

export function listarProveedores(codempresa) {
    return axios.get(`${baseURL}/efuel/proveedor/findAll?codempresa=${codempresa}`)
}

export function buscarProveedores(idProveedor) {
    return axios.get(`${baseURL}/efuel/proveedor/findOne?idProveedor=${idProveedor}`)
}

export function crearProveedores(razsocial, name, codempresa) {
    return axios.post(`${baseURL}/efuel/proveedor/create`, {
        razsocial: razsocial,
        name: name,
        codempresa: codempresa
    })
}

export function eliminarProveedores(id) {
    return axios.delete(`${baseURL}/efuel/proveedor/delete?id=${id}`)
}

export function editarProveedores(razSocial, nombre, id) {
    return axios.put(`${baseURL}/efuel/proveedor/update`, {
        razsocial: razSocial,
        nombre: nombre,
        id: id
    })
}

export function reporteRefillsProveedores(fechaDesde, fechaHasta, codempresa) {
    return axios.post(`${baseURL}/efuel/proveedor/report/refill`, {
        codempresa: codempresa,
        fechaDesde: fechaDesde,
        fechaHasta: fechaHasta
    })
}
